<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
      <span></span>
      <button class="btn btn-dark mx-2" @click.prevent="sortSwitch('return')"></button>
      <button class="btn btn-dark" @click.prevent="openModal(true)">Create a new product</button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120" class="hover-style" @click="sortSwitch('category')">
            <span v-if="sortItem === 'category'">
              <i class=" fas fa-angle-down text-info" v-if="status.isReverse"></i>
            </span>
          </th>
          <th class="hover-style" @click="sortSwitch('title')">
            <span v-if="sortItem === 'title'">
              <i class=" fas fa-angle-down text-info" v-if="status.isReverse"></i>
            </span>
          </th>
          <th width="120" class="hover-style" @click="sortSwitch('origin_price')">
            <span v-if="sortItem === 'origin_price'">
              <i class=" fas fa-angle-up text-info" v-if="status.isReverse"></i>
              <i class=" fas fa-angle-down text-info" v-else></i>
            </span>
          </th>
          <th width="120" class="hover-style" @click="sortSwitch('price')">
            <span v-if="sortItem === 'price'">
              <i class=" fas fa-angle-up text-info" v-if="status.isReverse"></i>
              <i class=" fas fa-angle-down text-info" v-else></i>
            </span>
          </th>
          <th width="120" class="hover-style" @click="sortSwitch('is_enabled')">
            <span v-if="sortItem === 'is_enabled'">
              <i class=" fas fa-angle-up text-info" v-if="status.isReverse"></i>
              <i class=" fas fa-angle-down text-info" v-else></i>
            </span>
          </th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in newData" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-right">
            {{ item.origin_price | currency }}
          </td>
          <td class="text-right">
            {{ item.price | currency }}
          </td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else class="text-danger"></span>
          </td>
          <td>
            <button class="btn btn-outline-primary mr-1"
              @click.prevent="openModal(false, false, item)"></button>
            <button class="btn btn-outline-danger" @click.prevent="openModal(false, true, item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- Pagination -->
    <Pagination :paginationComponent="pagination" v-on:turnpage="getProducts" v-if="!status.isPagination"></Pagination>
    <!-- Modal for create a new product-->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input type="text" class="form-control" id="image"
                    placeholder="" v-model="tempProduct.imageUrl">
                </div>
                <div class="form-group">
                  <label for="customFile"> 
                    <i class="fas fa-spinner fa-spin" v-if="status.uploadFile == true"></i>
                  </label>
                  <input type="file" id="customFile" class="form-control"
                    ref="files" @change="uploadFile">
                </div>
                <img class="img-fluid" :src="tempProduct.imageUrl" alt="">
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input type="text" class="form-control" id="title"
                    placeholder="" v-model="tempProduct.title">
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <!-- <input type="text" class="form-control" id="category"
                      placeholder="" v-model="tempProduct.category"> -->
                      <select class="form-control" v-model="tempProduct.category">
                        <option></option>
                        <option></option>
                        <option></option>
                        <option></option>
                        <option></option>
                        <option></option>
                        <option></option>
                        <option></option>
                        <option></option>
                      </select>
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="unit" class="form-control" id="unit"
                      placeholder="" v-model="tempProduct.unit">
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                  <label for="origin_price"></label>
                    <input type="number" class="form-control mt-4" id="origin_price"
                      placeholder="" v-model="tempProduct.origin_price">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <!-- <input type="number" class="form-control" id="price"
                      placeholder="" v-model="tempProduct.price"> -->
                      <div class="form-check">
                        <input class="form-check-input" type="checkbox" id="defaultCheck1"
                          v-model="disabled" @click="checkboxTorF" :true-value="1" :false-value="0">
                        <label class="form-check-label" for="defaultCheck1" style="font-size:.5rem">
                          
                        </label>
                        <input type="number" class="form-control" id="price" :disabled="disabled === 1"
                          placeholder="" v-model="tempProduct.price">
                    </div>
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description"></label>
                  <textarea type="text" class="form-control" id="description"
                    placeholder="" rows="5" v-model="tempProduct.description"></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea type="text" class="form-control" id="content"
                    placeholder="" rows="5" v-model="tempProduct.content"></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox"
                      id="is_enabled" v-model="tempProduct.is_enabled" :true-value="1" :false-value="0">
                    <label class="form-check-label" for="is_enabled">
                      
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click.prevent="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal for makesure to delete the product-->
    <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{ tempProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click.prevent="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery' //  jQuery
import Pagination from '../components/Pagination'
export default {
  data () {
    return {
      allProducts: [],
      products: [],
      tempProduct: {},
      pagination: {},
      isNew: false,
      isDelete: false,
      isLoading: false,
      status: {
        uploadFile: false,
        isReverse: false,
        isPagination: false // Pagination 
      },
      disabled: 0,
      sortItem: ''
    }
  },
  methods: {
    getAllProducts () {
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOMER_PATH}/products/all`
      const vm = this
      this.$http.get(api).then((response) => {
        console.log(api)
        console.log(response.data)
        vm.allProducts = response.data.products
        console.log(vm.allProducts)
      })
    },
    getProducts (page = 1) {
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOMER_PATH}/admin/products?page=${page}`
      const vm = this
      vm.isLoading = true
      this.$http.get(api).then((response) => {
        console.log(api)
        console.log(response.data)
        vm.products = response.data.products
        vm.isLoading = false
        console.log(vm.products)
        vm.pagination = response.data.pagination
        console.log(vm.pagination)
      })
    },
    openModal (isNew, isDelete, item) {
      if (!isDelete) { //  Modal( or ), !isDelete 
        if (isNew) { //  isNew = true ;  isNew = false 
          this.tempProduct = {}
          this.isNew = true
          this.disabled = false //  checkbox 
        } else {
          //  this.tempProduct = item  item  tempProduct
          this.tempProduct = Object.assign({}, item) // Object.assign  ES6 https://developer.mozilla.org/zh-TW/docs/Web/JavaScript/Reference/Global_Objects/Object/assign
          this.isNew = false
          if (this.tempProduct.price === this.tempProduct.origin_price) { //  checkbox .
            this.disabled = 1
          } else {
            this.disabled = 0
          }
        }
        $('#productModal').modal('show') //  Modal
      } else { // isDelete = true
        $('#delProductModal').modal('show')
        this.tempProduct = Object.assign({}, item)
        this.isDelete = true
      }
    },
    updateProduct () {
      // httpMethod = 'post', 'put', 'delete'  '', '', ''
      let api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOMER_PATH}/admin/product`
      let httpMethod = 'post'
      const vm = this
      // console.log(vm.isNew, vm.isDelete)
      if (!vm.isNew && !vm.isDelete) { //    api  (httpMethod)
        api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOMER_PATH}/admin/product/${vm.tempProduct.id}`
        httpMethod = 'put'
      } else if (!vm.isNew && vm.isDelete) { //    api  (httpMethod)
        api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOMER_PATH}/admin/product/${vm.tempProduct.id}`
        httpMethod = 'delete'
        vm.isDelete = false
      }
      this.$http[httpMethod](api, { data: vm.tempProduct }).then((response) => {
        console.log(response.data)
        if (response.data) {
          $('#productModal').modal('hide')
          $('#delProductModal').modal('hide')
          // vm.$bus.$emit('messsage:push', response.data.message, 'success')
          vm.$store.dispatch('updateMessage', { message: response.data.message, status: 'success' })
          vm.getProducts(vm.pagination.current_page) //   
          vm.getAllProducts()
        } else {
          $('#productModal').modal('hide')
          // vm.$bus.$emit('messsage:push', response.data.message, 'danger')
          vm.$store.dispatch('updateMessage', { message: response.data.message, status: 'danger' })
          console.log('')
        }
      })
    },
    uploadFile () { // 
      // console.log(this)
      const uploadedFile = this.$refs.files.files[0] //  console.log(uploadedFile) 
      // console.log(uploadedFile)
      const vm = this
      vm.status.uploadFile = true
      const formData = new FormData() // https://developer.mozilla.org/zh-TW/docs/Web/API/FormData
      formData.append('file-to-upload', uploadedFile)
      const url = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_CUSTOMER_PATH}/admin/upload`
      this.$http.post(url, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then((response) => {
        console.log(response.data)
        if (response.data.success) {
          // vm.tempProduct.imageUrl = response.data.imageUrl
          // console.log(vm.tempProduct.imageUrl)
          // console.log(vm.tempProduct)
          vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl) //  $set 
          // this.$bus.$emit('messsage:push', '', 'success')
          vm.$store.dispatch('updateMessage', { message: '', status: 'success' })
          vm.status.uploadFile = false
        } else {
          console.log(response.data.message)
          // this.$bus.$emit('messsage:push', response.data.message, 'warning')
          vm.$store.dispatch('updateMessage', { message: response.data.message, status: 'warning' })
          vm.status.uploadFile = false
        }
      })
    },
    checkboxTorF () { //  checkbox 
      // console.log(this.disabled)
      if (!this.disabled) { //  =  input  disabled 
        console.log(1)
        this.tempProduct.price = this.tempProduct.origin_price
      } else {
        this.tempProduct.price = ''
        console.log(0)
      }
    },
    sortSwitch (item) { // 
      this.status.isPagination = true //  Pagination 
      this.sortItem = item
      if (this.sortItem === 'category' || this.sortItem === 'title') {
        console.log(this.sortItem, this.status.isReverse)
        this.status.isReverse = true
      } else if (this.sortItem === 'return') {
        this.status.isPagination = false //  Pagination 
      } else {
        console.log(this.sortItem, typeof (this.sortItem), this.status.isReverse)
        this.status.isReverse = !this.status.isReverse // 
      }
    }
  },
  computed: {
    newData () {
      const vm = this
      if (!this.status.isPagination) {
        return this.products
      } else if (this.sortItem === 'category' || this.sortItem === 'title') {
        let newData = vm.allProducts.sort(function (a, b) {
          var A = a[vm.sortItem].toUpperCase() // ignore upper and lowercase
          var B = b[vm.sortItem].toUpperCase() // ignore upper and lowercase
          if (A < B) {
            return -1
          }
          if (A > B) {
            return 1
          }
          return 0
        })
        return newData
      } else if (this.status.isReverse) {
        let newData = vm.allProducts.sort(function (a, b) {
          return a[vm.sortItem] - b[vm.sortItem]
        })
        return newData
      } else if (!this.status.isReverse) {
        let newData = vm.allProducts.sort(function (a, b) {
          return b[vm.sortItem] - a[vm.sortItem]
        })
        return newData
      }
    }
  },
  components: {
    Pagination
  },
  created () {
    this.getAllProducts()
    this.getProducts()
    console.log(this.disabled)
  }
}
</script>

<style lang="scss" scoped>
  .hover-style {
    &:hover {
      cursor: pointer;
    }
  }
</style>
